.DList {
  padding: 0.426667rem 0.32rem;
}

.DList-Show {
  background: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.16rem 0.213333rem;
  box-shadow: 0 0.053333rem 0.053333rem 0 rgba(0, 0, 0, 0.04);
  margin-bottom: 0.213333rem;
}

.DList-Show:last-of-type {
  margin-bottom: 0;
}

.DList-Show-Text {
  width: calc(100% - 2.666667rem);
}

.DList-Show-Text p {
  color: #333333;
  font-size: 0.426667rem;
  line-height: 0.693333rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.DList-Show-Text p:nth-of-type(1) {
  font-weight: bolder;
}

.DList-Show-Text p:nth-of-type(2) {
  font-size: 0.373333rem;
  font-weight: 0.586667rem;
}

.DList-Show-Arrow {
  width: 0.533333rem;
  height: 0.533333rem;
}
.Index-Head {
  padding: 0.533333rem 0.32rem 0.266667rem;
  background: #C5D9E8;
}

.Index-Head div {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 0.32rem;
}

.Index-Head div:nth-of-type(1) img {
  width: 2.133333rem;
  height: 2.133333rem;
  border-radius: 50%;
}

.Index-Head div:nth-of-type(2) p {
  margin-right: 0.213333rem;
}
.Content-HeadTitle {
  padding: 0.8rem 0.32rem;
  background: #C5D9E8;
  margin-bottom: 0.533333rem;
}

.Content-HeadTitle-title {
  padding-top: 0.533333rem;
  display: flex;
  justify-content: left;
  align-items: center;
}

.Content-HeadTitle-title-name {
  font-size: 0.56rem;
  line-height: 0.826667rem;
  font-weight: bolder;
  color: #333333;
  margin-left: 0.32rem;
}

.Content-HeadTitle-content {
  margin-top: 0.8rem;
}

.Content-List {
  padding: 0.16rem 0.213333rem;
}

.Content-List div {
  display: flex;
  justify-content: space-between;
  padding: 0.32rem 0.16rem;
  border-bottom: 0.026667rem solid #C5D9E8;
  font-size: 16;
  margin-bottom: 0.32rem;
}
.CommonTitle h1 {
  font-size: 18;
  font-weight: bolder;
  margin-bottom: 8;
}

.CommonTitle-title {
  display: flex;
  justify-content: flex-start;
  margin: 0.16rem 0;
}

.CommonTitle-title p:nth-of-type(1) {
  width: 0.08rem;
  height: 0.64rem;
  margin-right: 0.213333rem;
  background: #78A4FA;
}

.CommonTitle-title p:nth-of-type(2) {
  font-weight: bold;
  line-height: 0.64rem;
}

.CommonTitle-content {
  font-size: 0.373333rem;
  line-height: 0.64rem;
  padding: 0.16rem 0;
  text-indent: 2em;
}

.CommonTitle-content p {
  display: inline-block;
  width: 0.106667rem;
  height: 0.533333rem;
  background: #78A4FA;
}
.ToolsModal {
  padding: 0.16rem 0.32rem;
}

.ToolsModal-div {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  font-family: "microsoft yahei";
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  font-family: "微软雅黑";
  word-break: break-all;
}

fieldset,
img {
  border: 0;
}

ul,
ol,
li {
  list-style: none;
  font-family: 'Microsoft YaHei';
}

/* remember to define focus styles! */

:focus {
  outline: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var,
optgroup {
  font-style: normal;
  font-weight: normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

abbr,
acronym {
  border: 0;
  font-feature-settings: normal;
  font-variant: normal;
}

input,
button,
textarea,
select,
optgroup,
option {
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
  background-color: #fff;
  border: none;
}

code,
kbd,
samp,
tt {
  font-size: 100%;
}

a {
  color: #333;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/*@purpose To enable resizing for IE */

/*@branch For IE6-Win, IE7-Win */

input,
button,
textarea {
  *font-size: 100%;
}

ol,
ul {
  list-style: none;
}

/* tables still need 'cellspacing="0"' in the markup */

table,
tr,
td {
  border-collapse: collapse;
  border-spacing: 0;
  margin: 0;
  padding: 0;
}

caption,
th {
  text-align: left;
}

sup,
sub {
  font-size: 100%;
  vertical-align: baseline;
}

/* remember to highlight anchors and inserts somehow! */

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

/**Common Css**/

.fr {
  float: right;
  *display: inline;
}

.fl {
  float: left;
  *display: inline;
}

/*文本超出显示省略号*/

.ehn,
.otw,
.tow {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

/*清除浮动*/

.clearfix {
  clear: both;
  *zoom: 1;
}

.clearfix:after {
  display: block;
  clear: both;
  content: "\0020";
  visibility: hidden;
  height: 0;
}
